body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 10;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minute {
  transform-origin: 50% 100%;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute;
  position: absolute;
  top: 20vmin;
}

.stopwatch__second {
  transform-origin: 50% 100%;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second;
  position: absolute;
  top: 2vmin;
}

.stopwatch--speed-up .stopwatch__second {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minute {
  animation-duration: 600s;
}

@keyframes rotate-second {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-minute {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
